.lenderItem {
  background-size: contain;
  background-repeat: no-repeat;
  border-radius: 20px;
  margin-top: -3px;
  padding: 20px;
  min-height: 300px;
  box-shadow: 0 6px 11px 0 rgba(0, 0, 0, 0.12);
  background-color: #ffffff;
  transition: all 0.2s ease;
}
.lenderItem .itemTitle {
  letter-spacing: 0.25px;
  line-height: 1.357em;
  font-size: 1.75em;
  letter-spacing: 0.12px;
  line-height: 1.786em;
  font-size: 2em;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
  border-bottom: 1px solid #e5e5e5;
  font-family: Merriweather Sans;
  font-weight: 700;
  min-height: 4em;
  max-height: 4em;
  -webkit-line-clamp: 2;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
}
.lenderItem .itemDate {
  font-size: 1.563em;
  letter-spacing: 0.2px;
  line-height: 1.8em;
  font-family: Merriweather Sans;
  padding: 15px 0px;
  font-size: 1.25em;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: left;
  color: #3bb859;
}
.lenderItem .itemDescrip {
  font-size: 1.5em;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.8;
  letter-spacing: normal;
  text-align: left;
  color: #333333;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
}
.lenderItem .readMore {
  font-size: 1.25em;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.67;
  letter-spacing: normal;
  text-align: left;
  color: #3bb859;
  margin-top: 20px;
}
.lenderItem .hexagone {
  position: absolute;
  top: -4.781em;
  right: 3.125em;
  width: 11.063em;
}
.lenderItem:hover {
  background-color: #0dad46;
}
.lenderItem:hover .itemTitle {
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
}
.lenderItem:hover .itemDate {
  color: #ffffff;
}
.lenderItem:hover .itemDescrip {
  color: #ffffff;
}
.lenderItem:hover .readMore {
  color: #ffffff;
  text-decoration: underline;
}
.lenderItem:hover .hexagone {
  position: absolute;
  top: -4.781em;
  right: 3.125em;
  width: 11.063em;
}
